.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

input {
  width: 150%;
  padding: 10px;
  background-color: #282c34;
  color: white;
  border-radius: 5px;
  border: none;
  margin: 2px;
}

button{
  font-size: large;
  margin:auto;
  border:none;
  background: transparent;
}

.input {
  display: flex;
  background-color: white;
  border-radius: 5px;
}
